/* General Styles */
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  background-color: #121212;
  color: #ffffff;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #121212; /* Darker background */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Dashboard Styles */
.dashboard {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px 0;
  width: 100%;
}

.coin-data {
  background-color: #424242;
  padding: 10px;
  border-radius: 8px;
  min-width: 150px; /* Adjust based on your needs */
  text-align: center;
}

.coin-data h3 {
  margin: 0 0 10px 0;
  font-size: 1.2rem;
}

.coin-data p {
  margin: 5px 0;
  font-size: 0.9rem; /* Smaller text */
}

/* Popup Styles */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.popup-inner {
  background-color: #333; /* Dark grey background */
  color: #fff; /* White lettering */
  padding: 20px;
  border-radius: 8px;
  font-size: 14px; /* Adjust the font size as needed */
  display: flex;
  flex-direction: column;
  align-items: center; /* Center horizontally */
}

.popup-inner .close-btn {
  margin-top: 20px; /* Adjust the top margin as needed */
  font-size: 16px; /* Increase the font size */
  padding: 10px 20px; /* Increase padding for better clickability */
}

.popup-inner ul {
  padding-left: 20px; /* Adjust the left padding as needed */
}

.popup-inner .close-btn:hover {
  background-color: #999; /* Darker grey background when hovering */
}

.popup-btn {
  margin-top: 20px;
}

/* Button Styles */
.button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  background-color: #0056b3;
}
